// 
import request from '@/utils/request.js'

/**
 * 
 * @param {String} account - 
 * @param {String} password - 
 * @return Promise
*/
export const userAccountLogin = ({ account, password }) => {
  return request('/login', 'post', { account, password })
}
/**
 * 
 * @param {String} mobile - 
 * @return Promise
*/
export const userMobileLoginMsg = (mobile) => {
  return request('/login/code', 'get', { mobile })
}

/**
 * 
 * @param {String} mobile -
 * @param {String} code - 123456
 * @return Promise
*/
export const userMobileLogin = ({ mobile, code }) => {
  return request('/login/code', 'get', { mobile, code })
}

/**
 * QQ
 * @param {String} unionId - QQ openId
 * @param {String} source -  1 PC
 * @return
*/
export const userQQLogin = (unionId, source = 1) => {
  return request('/login/social', 'post', { unionId, source })
}

/**
 * QQ
 * @param {String} mobile - 
 * @return Promise
*/
export const userQQBindCode = (mobile) => {
  return request('/login/social/code', 'get', { mobile })
}

/**
 * QQ-
 * @param {String} unionId - QQopenId
 * @param {String} mobile - 
 * @param {String} code - 
 * @return Promise
*/
export const userQQBindLogin = ({ unionId, mobile, code }) => {
  return request('/login/social/bind', 'post', { unionId, mobile, code })
}

/**
 * 
 * @param {String} account - 
 * @return Promise
*/
export const userAccountCheck = (account) => {
  return request('/register/check', 'get', { account })
}

/**
 * QQ
 * @param {String} mobile - 
 * @return Promise
*/
export const userQQPatchCode = (mobile) => {
  return request('/register/code', 'get', { mobile })
}

/**
 * QQ-
 * @param {String} unionId - QQopenId
 * @param {String} mobile - 
 * @param {String} code - 
 * @param {String} account - 
 * @param {String} password - 
 * @return Promise
*/
export const userQQPatchLogin = ({ unionId, mobile, code, account, password }) => {
  return request(`/login/social/${unionId}/complement`, 'post', { unionId, mobile, code, account, password })
}
