import { userAccountCheck } from '@/api/user'

// vee-validate
export default {
  // 
  account (value) {
    if (!value) return ''
    // 6-20
    if (!/^[a-zA-Z]\w{5,19}$/.test(value)) return '6-20'
    return true
  },
  // 
  async accountApi (value) {
    if (!value) return ''
    if (!/^[a-zA-Z]\w{5,19}$/.test(value)) return '6-20'
    // 
    // userAccountCheck 
    const data = await userAccountCheck(value)
    if (data.result.valid) return ''
    return true
  },
  // 
  password (value) {
    if (!value) return ''
    // 6-24
    if (!/^\w{6,24}$/.test(value)) return '6-24'
    return true
  },
  // 
  rePassword (value, {
    form
  }) {
    if (!value) return ''
    if (!/^\w{6,24}$/.test(value)) return '6-24'
    // form
    if (value !== form.password) return ''
    return true
  },
  // 
  mobile (value) {
    if (!value) return ''
    // : 1 3-9 9
    if (!/^1[3-9]\d{9}$/.test(value)) return ''
    return true
  },
  // 
  code (value) {
    if (!value) return ''
    // 6
    if (!/^\d{6}$/.test(value)) return '6'
    return true
  },
  // 
  isAgree (value) {
    if (!value) return ''
    return true
  }
}
